<template>
  <div class="home">
    <landing :section="{}">
      <div style="color: transparent">g</div>
      <!-- <monitor :screens="screens" /> -->
    </landing>
    <article class="about">
      <h3>About us <span>icom</span></h3>
      <p></p>
    </article>
    <slider />
    <article class="contact" id="contact">
      <h3>Contact us</h3>
      <div class="container">
        <img src="../assets/plane.png" alt="plane" />
        <div>
          <squares class="squares" />
          <squares class="squares" />
          <h4>
            GET IN TOUCH <br />
            WITH US
          </h4>
          <ul>
            <li><i class="fas fa-location-dot"></i>Syria</li>
            <li><i class="fas fa-phone"></i>+1 567-678-4590</li>
            <li><i class="fas fa-envelope"></i>hricom@gmail.com</li>
          </ul>
          <div class="links"><a href=""></a><a href=""></a><a href=""></a></div>
        </div>
        <form @submit.prevent>
          <label for="name">Name</label>
          <input
            type="text"
            id="name"
            placeholder="your name"
            v-model="contactForm.name"
          />
          <label for="email">Email</label>
          <input
            type="email"
            id="email"
            placeholder="your email"
            v-model="contactForm.email"
          />
          <label for="service">Service</label>
          <select id="service" v-model="contactForm.service">
            <option value="Dropdown">Dropdown</option>
          </select>
          <label for="phone">Phone</label>
          <input
            type="text"
            id="phone"
            placeholder="your phone"
            v-model="contactForm.phone"
          />
          <label for="message">Message</label>
          <textarea
            id="message"
            placeholder="your message"
            v-model="contactForm.message"
          ></textarea>
          <button @click="sendMessage()">SUBMIT</button>
        </form>
      </div>
