<template>
  <section class="services">
    <div>
      <button @click="previous()">
        <i class="fas fa-angle-left"></i>
      </button>
      <button @click="next()">
        <i class="fas fa-angle-right"></i>
      </button>
    </div>
    <div
      id="sections"
      :style="`--current-step: 0; --step-amount: -${stepAmount}px`"
    >
      <article
        v-for="(section, index) in sections"
        :key="index"
        :data-index="index + 1"
        :style="`--far-from-start: ${-(index * 340 + 160)}px`"
      >
        <figure>
          <img :src="section.poster" :alt="section.name" class="poster" />
          <img :src="section.icon" :alt="section.name" class="icon" />
        </figure>
        <h4>{{ section.name }}</h4>
        <p>{{ section.description }}</p>
        <button class="extend" @click="extend($event, true)">
          Browse project
        </button>
        <button class="close" @click="extend($event, false)">
          <i class="fas fa-times fa-2x"></i>
        </button>
      </article>
    </div>
  </section>
</template>

<script>
export default {
  name: "slider",
  data() {
    return {
      sections: this.$store.state.sections,
      currentStep: 0,
      stepAmount: 0,
      STEP_NUMBERS: 0,
    };
  },
  watch: {
    currentStep(newStep) {
      document
        .getElementById("sections")
        .style.setProperty("--current-step", newStep);
    },
  },
  methods: {
    setSteps() {
      const SECTION_WIDTH = 340;
      const ALL_SECTIONS_WIDTH = SECTION_WIDTH * this.sections.length;
      const SLIDER_CONTROL_WIDTH = 160;
      const SHOW_AREA = window.innerWidth - SLIDER_CONTROL_WIDTH;
      const MAX_HIDDEN_AREA = ALL_SECTIONS_WIDTH - SHOW_AREA;
      this.STEP_NUMBERS = Math.floor(MAX_HIDDEN_AREA / SECTION_WIDTH);
      this.stepAmount = MAX_HIDDEN_AREA / this.STEP_NUMBERS;
    },
    next() {
      this.setSteps();
      if (this.currentStep < this.STEP_NUMBERS) {
        this.currentStep++;
      }
    },
    previous() {
      if (this.currentStep > 0) {
        this.currentStep--;
      }
    },
    extend(e, decision) {
      if (decision) {
        e.currentTarget.parentElement.classList.add("extended");
        // const thisSection = e.currentTarget.parentElement;
        // document.getElementById("sections").style.height =
        //   thisSection.offsetHeight + "px";
      } else {
        e.currentTarget.parentElement.classList.remove("extended");
        // document.getElementById("sections").style.height = "auto";
      }
    },
  },
  mounted() {
    this.setSteps();
    window.addEventListener("resize", this.setSteps);
  },
};
</script>

<style lang="scss" scoped>
section.services {
  background-color: #27153d;
  width: 100%;
  overflow: hidden;
  margin: 20px 0;
  padding: 0;
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  div#sections {
    display: inline-flex;
    flex-flow: row nowrap;
    left: 0;
    transition: all 0.2s ease-out;
    & > article {
      transform: translateX(calc(var(--step-amount) * var(--current-step)));
      height: 100vh;
      width: 340px;
      max-width: 100%;
      flex-shrink: 0;
      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-end;
      align-items: center;
      position: relative;
      padding: 40px 20px;
      color: white;
      transition: all 0.8s ease;
      border-left: 2px solid rgba(255, 255, 255, 0.05);
      overflow: hidden;

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(
          60deg,
          transparent 0%,
          rgba(255, 255, 255, 0.3) 100%
        );
        opacity: 0;
        transition: opacity 0.2s ease;
        z-index: 2;
      }
      &::before {
        content: attr(data-index);
        position: absolute;
        top: 20px;
        left: 10px;
        z-index: 1;
      }
      figure {
        margin: 0;
        padding: 0;
        position: relative;
        img {
          width: 100%;
          max-width: 400px;
          margin: 20px 0;
          z-index: 3;
          transition: opacity 0.4s ease-out;
          &.poster {
            opacity: 0;
          }
          &.icon {
            width: 80px;
            position: absolute;
            margin: auto auto;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
      &:hover {
        &::after {
          opacity: 1;
        }
        img {
          &.poster {
            opacity: 1;
          }
          &.icon {
            opacity: 0;
          }
        }
      }

      &.extended {
        transform: translateX(var(--far-from-start));
        width: 100vw;
        max-width: none;
        height: 100%;
        background-color: #27153d;
        z-index: 4;
        display: block;
        padding-top: 60px;
        button.close {
          display: block;
        }
        button.extend {
          display: none;
        }
        img {
          &.poster {
            opacity: 1;
          }
          &.icon {
            opacity: 0;
          }
        }
      }
      p {
        width: 100%;
        max-width: 600px;
        font-size: 0.8em;
        color: var(--accent);
        line-height: 1.6em;
        margin: 0 auto;
      }
      button {
        font-family: inherit;
        z-index: 3;
        border: none;
        outline: none;
        color: white;
        font-size: 0.8em;
        &.extend {
          margin-top: 10px;
          padding: 10px 40px 10px 10px;
          font-weight: bold;
          box-shadow: 0 0 8px 2px var(--accent) inset;
          position: relative;
          overflow: hidden;
          background: transparent;
          &::after {
            content: "\f105";
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            top: 0;
            right: 0;
            width: 30px;
            height: 100%;
            background-color: var(--accent);
            margin-left: 10px;
            opacity: 0.6;
            font-family: "Font Awesome 5 Free";
          }
          &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: var(--accent);
            transform: translateX(-100%);
            transition: transform 0.2s ease-out;
            z-index: -1;
          }
          &:hover::before {
            transform: translateX(0);
          }
        }
        &.close {
          position: fixed;
          top: 20px;
          right: 40px;
          border-radius: 50%;
          background-color: rgba(238, 27, 94, 0.6);
          display: none;
          i {
            margin: 4px;
          }
        }
      }
    }
  }
  div:not(#sections) {
    z-index: 4;
    display: flex;
    flex-shrink: 0;
    width: 160px;
    flex-flow: row nowrap;
    align-items: center;
    padding: 0;
    padding-left: 20px;
    background: linear-gradient(
      220.1deg,
      rgba(32, 23, 72, 1) 0%,
      rgba(75, 43, 132, 1) 100%
    );
    &::after {
      display: none;
    }
    button {
      width: 30px;
      height: 30px;
      background: none;
      padding: 10px;
      margin: 10px;
      border: 2px solid white;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      transition: all 0.1s ease;
      &:hover {
        transform: scale(1.1);
        border-color: var(--accent);
      }
    }
  }
}
@media (max-width: 1020px) {
  section.services {
    div#sections {
      flex-flow: row wrap;
      article {
        justify-content: center;
        border-bottom: 2px solid rgba(255, 255, 255, 0.05);
        flex-grow: 1;
        &.extended {
          transform: translate(0);
          height: 100vh;
        }
      }
    }
    & > div:not(#sections) {
      display: none;
    }
  }
}
</style>
