<template>
  <header class="landing">
    <ul class="social-links">
      <li>
        <a href=""><i class="fab fa-twitter"></i></a>
      </li>
      <li>
        <a href=""><i class="fab fa-facebook"></i></a>
      </li>
      <li>
        <a href=""><i class="fab fa-instagram"></i></a>
      </li>
      <li>
        <a href=""> <i class="fab fa-dribbble"></i></a>
      </li>
    </ul>
    <nav>
      <span class="logo">icom</span>
      <ul class="main">
        <li><router-link to="/">Home</router-link></li>
        <li><router-link to="/">About us</router-link></li>
        <li><router-link to="/">Services</router-link></li>
        <li class="portfolio">
          <router-link to="/">Portfolio</router-link>
          <ul>
            <li v-for="section in $store.state.sections" :key="section.name">
              <router-link :to="section.link">{{ section.name }}</router-link>
            </li>
          </ul>
        </li>
        <li class="contact-us">
          <a href="#contact" @click="$router.push('/')">Contact us</a>
        </li>
      </ul>
      <button @click="toggleMenu">
        <i class="fas fa-bars fa-2x"></i>
        <i class="fas fa-times fa-2x"></i>
      </button>
    </nav>
    <section>
      <img v-if="section.image" :src="section.image" :alt="section.name" />
      <h2 v-if="section.name">{{ section.name }}</h2>
      <slot></slot>
    </section>
  </header>
</template>

<script>
export default {
  name: "landing",
  props: {
    section: {
      type: Object,
    },
  },
  methods: {
    toggleMenu() {
      document.querySelector("header nav").classList.toggle("menu-shown");
    },
  },
  mounted() {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 0) {
        document.querySelector("nav").classList.add("below-header");
      } else {
        document.querySelector("nav").classList.remove("below-header");
      }
    });
  },
};
</script>

<style lang="scss">
header {
  height: 100vh;
  width: 100%;
  color: white;
  background: linear-gradient(
    220.1deg,
    rgba(32, 23, 72, 1) 0%,
    rgba(75, 43, 132, 1) 100%
  );
  display: flex;
  flex-flow: column nowrap;
  position: relative;
  ul.social-links {
    list-style: none;
    position: absolute;
    top: 50%;
    right: 3%;
    transform: translate(-50%, -50%);
    li {
      margin: 30px 0;
    }
  }
  section {
    display: flex;
    flex-flow: column wrap;
    height: 400px;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    margin: auto;
    h2 {
      font-size: max(4vw, 2em);
      max-width: max(65%, 320px);
    }
    img {
      height: 100%;
      max-height: 40vw;
    }
  }
  nav {
    position: fixed;
    top: 0;
    z-index: 5;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: 10px 60px;
    justify-content: space-between;
    transition: background-color 0.2s ease;
    &.below-header {
      background: rgba(0, 0, 0, 0.6);
    }
    button {
      display: none;
      z-index: 5;
      background: transparent;
      border: none;
      outline: none;
      color: white;
    }
    span.logo {
      font-size: 1.6em;
      font-weight: bold;
    }
    ul.main {
      list-style: none;
      padding: 0;
      & > li {
        margin-left: 4em;
        display: inline-block;
        a {
          padding: 6px 10px;
          transition: background-color 0.2s ease;
          border-radius: 40px;
          &:hover {
            background-color: rgba(255, 255, 255, 0.1);
          }
        }
        &.contact-us {
          margin-left: 6em;
          a {
            border: 2px solid var(--accent);
            &:hover {
              background-color: var(--accent);
            }
          }
        }
        &.portfolio {
          ul {
            visibility: hidden;
            opacity: 0;
            position: fixed;
            padding: 14px 20px;
            margin: 0;
            background: rgba(0, 0, 0, 0.8);
            border-radius: 10px;
            margin-left: -20px;
            margin-top: 20px;
            overflow: hidden;
            transition: visibility 0.4s ease, opacity 0.4s ease;
            li {
              display: block;
              margin: 10px 0;
            }
          }
          &:hover {
            ul {
              visibility: visible;
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 950px) {
  header {
    nav {
      padding: 20px 30px;
      .fa-bars {
        display: inline;
      }
      .fa-times {
        display: none;
      }
      &.menu-shown {
        ul {
          transform: translateY(0) !important;
          li.contact-us {
            margin-left: 0;
            a {
              border-color: white;
              &:hover {
                background-color: white;
                color: var(--accent);
              }
            }
          }
        }
        .fa-bars {
          display: none;
        }
        .fa-times {
          display: inline;
        }
      }
      button {
        display: block;
      }
      ul.main {
        margin: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        background-color: rgba(238, 27, 94, 0.9);
        transform: translateY(-100%);
        transition: transform 0.4s ease-out;
        li {
          font-size: 1.2em;
          display: block !important;
          margin: 2em 0;
          &.portfolio {
            ul {
              font-size: 0.8em;
              width: 100%;
              margin-top: 20px;
              margin-left: 0;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 690px) {
  header {
    height: 50vh;
  }
}
</style>
